import { FC, useEffect, useState } from 'react';
import styled from 'styled-components';

const AnimeQuote: FC = () => {
  const [animeQuote, setAnimeQuote] = useState({
    quote: '',
    character: '',
    anime: '',
  });

  interface AnimeQuote {
    quote: string;
    character: string;
    anime: string;
  }

  useEffect(() => {
    fetch('https://animechan.vercel.app/api/random')
      .then((response) => response.json())
      .then((quote: AnimeQuote) => setAnimeQuote(quote));
  }, []);
  return (
    <AnimeQuoteWrapper>
      <p>
        <i>&quot;{animeQuote.quote}&quot;</i> - {animeQuote.character},{' '}
        <b>{animeQuote.anime}</b>
      </p>
    </AnimeQuoteWrapper>
  );
};

export default AnimeQuote;

const AnimeQuoteWrapper = styled.div`
  margin: 0px 20px;
`;
